@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

/* Custom animation keyframes */
@keyframes blob {
  0% {
    transform: translate(0px, 0px) scale(1);
  }
  33% {
    transform: translate(30px, -20px) scale(1.05);
  }
  66% {
    transform: translate(-20px, 20px) scale(0.95);
  }
  100% {
    transform: translate(0px, 0px) scale(1);
  }
}

@layer utilities {
  .animation-delay-2000 {
    animation-delay: 2s;
  }
  .animation-delay-4000 {
    animation-delay: 4s;
  }
  .animate-blob {
    animation: blob 10s infinite;
  }
}

/* Glass morphism */
.bg-glassmorphism {
  @apply bg-white/10 backdrop-blur-lg border border-white/20;
}

/* Form elements */
.input {
  @apply w-full px-4 py-3 bg-white/5 border border-gray-300/20 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500 text-white placeholder-gray-400 transition duration-200;
}

.label {
  @apply block text-gray-200 mb-2;
}

.btn {
  @apply py-3 px-4 font-medium rounded-lg shadow-lg transform hover:scale-[1.02] transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-offset-2;
}

.btn-primary {
  @apply bg-gradient-to-r from-purple-600 to-indigo-600 hover:from-purple-700 hover:to-indigo-700 text-white focus:ring-purple-500;
}

.btn-outline {
  @apply bg-transparent border border-indigo-400 text-white hover:bg-indigo-700/20 focus:ring-indigo-500;
}

